.top-bar {
  padding-block: 24px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  @include on-tablet {
    padding-block: 32px;
   }

   @include on-desktop {
    max-width: 1020px;
  }

  &__icon {
    display: flex;
    gap: 24px;
  }

  &__logo-link {
    display: flex;
  }

  &-logo {
    height: 16px;

    @include hover(transform, scale(1,2));

    @include on-tablet {
      height: 24px;
    }
  }
}
